#quiz__image__wrapper {
    position: relative;
    margin-top: 1em; }
#quiz__svg {
    border-radius: 1.2em;

    max-width: 400px;
    aspect-ratio: 2 / 2.99; }

.quiz__image {
    width: 100%;

    &--blur {
        filter: blur(30px); } }

circle {
    opacity: 1;
    -webkit-transition: opacity 100ms linear;
    transition: opacity 100ms linear; }

.checkmark__circle {
    stroke-dasharray: 166;
    stroke-dashoffset: 166;
    stroke-width: 2;
    stroke-miterlimit: 10;
    stroke: #f7c10f;
    fill: none;
    animation: stroke 0.6s cubic-bezier(0.65, 0, 0.45, 1) forwards; }

.checkmark {
    position: absolute;
    left: calc( 50% - 28px );
    top: calc( 50% - 28px );
    width: 56px;
    height: 56px;
    border-radius: 50%;
    display: block;
    stroke-width: 2;
    stroke: #fff;
    stroke-miterlimit: 10;
    box-shadow: inset 0px 0px 0px #f7c10f;
    animation: fill .4s ease-in-out .4s forwards, scale .3s ease-in-out .9s both;

    &__check {
        transform-origin: 50% 50%;
        stroke-dasharray: 48;
        stroke-dashoffset: 48;
        animation: stroke 0.3s cubic-bezier(0.65, 0, 0.45, 1) 0.8s forwards; } }

@keyframes stroke {
    100% {
        stroke-dashoffset: 0; } }

@keyframes scale {

    0%,
    100% {
        transform: none; }

    50% {
        transform: scale3d(1.1, 1.1, 1); } }

@keyframes fill {
    100% {
        box-shadow: inset 0px 0px 0px 30px #f7c10f; } }

.--shake {
    animation: shake 0.82s cubic-bezier(.36,.07,.19,.97) both;
    transform: translate3d(0, 0, 0);
    backface-visibility: hidden;
    perspective: 1000px; }

@keyframes shake {
    10%, 90% {
        transform: translate3d(-1px, 0, 0); }

    20%, 80% {
        transform: translate3d(2px, 0, 0); }

    30%, 50%, 70% {
        transform: translate3d(-4px, 0, 0); }

    40%, 60% {
        transform: translate3d(4px, 0, 0); } }
