.web__container--gallery {
    padding-top: 1.5em;
    position: absolute;
    overflow: hidden;
    padding: 0 14%;
    top: 0;
    left: 0;
    height: 100%;
    width: 72%; }
.webgallery__header {
    margin-right: 200px;
    border-bottom: 1px solid #1f1f3159; }
.webgallery__h1 {
    margin: 30px 0 20px;
    font-size: 3.5em;
    color: #1F1F31; }
.webgallery__p {
    font-size: 1.2em;
    font-weight: 200;
    padding: 2% 20% 2% 0;
    border-bottom: 1px solid #1f1f3159;
    &--light {
        color: white; }
    &--dark {
        color: #1F1F31; } }
.web__webgallery {
    margin-top: 5%;
    position: static;
    justify-content: center;
    align-items: center;
    height: 100%;
    width: 100%;
    img {
        object-fit: contain;
        width: 100%;
        border-radius: 17px;
        cursor: pointer;
        &:first-child:hover {

            cursor: pointer; } }
    &--active {
        position: absolute;
        top: 0;
        margin: 0;
        left: 0;
        overflow-y: scroll;
        img {
            display: block;
            border-radius: 0px;
            &:first-child:hover {
                transform: none; } } }
    &--app {
        margin-top: 5em;
        text-align: center; }
    iframe {
        border-radius: 20px;
        aspect-ratio: 16 / 9;
        border: none;
        margin: auto;
        width: 50vw; } }

.web__article {
    position: relative;
    padding: 1.6em;
    grid-column: 2 / 3; }

.webgallery__triangle {
    position: absolute;
    top: 0;
    right: 0;
    width: 0;
    height: 0;
    border-top: 7em solid #fff;
    border-left: 8em solid transparent; }

.webgallery__close {
    position: absolute;
    right: 20px;
    top: -94px;
    width: 32px;
    height: 32px;
    opacity: .3;
    cursor: pointer; }

.webgallery__close:hover {
    opacity: 1;
    transform: scale(1.2);
    transition: 0.1s ease-in-out; }

.webgallery__close:before, .webgallery__close:after {
    position: absolute;
    left: 15px;
    content: " ";
    height: 30px;
    width: 4px;
    background-color: #333; }

.webgallery__close:before {
    transform: rotate(45deg); }

.webgallery__close:after {
    transform: rotate(-45deg); }

//Scroll custom
.web__webgallery::-webkit-scrollbar {
    width: 0px; }

@media (max-width: 650px) {
    .web__article {
        height: calc( 100vh - 40px);
        overflow: scroll; }
    .web__container--gallery {
        width: 100%;
        padding: 0;
        p {
            padding: 1em;
            font-size: 1em; } }
    .webgallery__header {
        display: none; }
    .web__list {
        li:last-child {
            margin-bottom: 100px; } }
    .webgallery__triangle {
        border-top: 75px solid white;
        border-left: 75px solid transparent; }
    .webgallery__close {
        top: -74px;
        right: 0px; } }
